@use "./../globals";
@use "./../fonts";
@use './../colors';
@use './../units';

$margin-left: 3px;
$margin-right: 9px;

.b-tag {
  color: colors.$bright-color;
  background-color: colors.$block-color;
  padding: 2px 10px;
  border-radius: units.$border-radius-base;
  cursor: default;
  display: inline-block;
  font-family: fonts.$font-alt;
  letter-spacing: fonts.$font-alt-text-letter-spacing;
  line-height: 16px;
  margin-left: $margin-left;
  margin-right: $margin-right;
  padding-top: 2px;
  text-decoration: none;
  transition: none;

  &[href] {
    // border-bottom-color: $link-4;
    cursor: pointer;

    &.active,
    &:hover {
      // background-color: $link-4;
      border-bottom-color: transparent;
      margin-left: 0;
      margin-right: $margin-right - $margin-left;
      padding-left: $margin-left;
      padding-right: $margin-left;
      text-decoration: none;
    }

    &:active {
      // background-color: $link-3;
    }

    &:last-child &:hover {
      margin-right: 0;
    }
  }

  &:last-child {
    margin-right: 3px;
  }

  &.narrow {
    margin-right: 5px;
  }

  a {
    transition: none;
    color: inherit;
  }
}
